  .angle-color{
      cursor:pointer;
      position: absolute;
    font-size:26px;
    -webkit-animation:transit 5s linear 2s infinite;
            animation:transit 5s linear 2s infinite;
  }

  .angle-color:hover{
      font-size:36px;
  }

  @-webkit-keyframes transit{
    0%   {color:#C5C6C7;opacity:0.6;}
    25%  {color:#C5C6C7;opacity:1;}
    50%  {color:#66FCF1;opacity:1;}
    100% {color:#66FCF1;opacity:0.6;}
  }

  @keyframes transit{
    0%   {color:#C5C6C7;opacity:0.6;}
    25%  {color:#C5C6C7;opacity:1;}
    50%  {color:#66FCF1;opacity:1;}
    100% {color:#66FCF1;opacity:0.6;}
  }

