.about-head{
    text-align:center;
    padding:10px;
    font-size: 3rem;
    /* color:#1F2833; */
}

.abt-txt{
    font-family: 'Cairo', sans-serif;
    text-align:justify;
}

.icon{
    cursor:pointer;
    font-size:4rem;
    padding:10px;
}



@media screen and (min-width:992px){
    .icon{
        font-size:5rem;}

}

.fa-html5{
    color:orange;
}
.fa-css3-alt{
    color:blue;
}
.fa-js{
    color:yellow;
}
.fa-node-js{
    color:green;
}
.icon-mongodb{
    color:#56a74c;
}
.fa-react{
    color:#6fd7f3;
}
.icon-c{
    color:#035697;
}
.icon-cplusplus{
    color:#035697;
}
.fa-docker{
    color:#2391e6;
}
.icon-jquery{
    color:#0868ab;
}
.fa-bootstrap{
    color:#533b78;
}
.fa-python{
    background: linear-gradient(to top right,#fed140,#3870a1 50%, #fed140 50%);
